<template>
<!-- Modal -->
<div class="modal fade" id="tipModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel"></h5>
        <button type="button" class="btn-close bg-color-primary m-0 px-2 py-0" data-bs-dismiss="modal" aria-label="Close">x</button>
      </div>
      <div class="modal-body">
        <!--  -->
        <ul class="px-0" v-if="pages === 'weather'">
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
            <img src="@/assets/image/tip/weather/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <p class="mb-2">
                
              </p>
              <img src="@/assets/image/tip/weather/.png" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <p class="mb-2">
                 GO 
                <br />
                
              </p>
              <img src="@/assets/image/tip/weather/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <ul>
                <li class="fw-bold"></li>
                <li class="mb-2"></li>
                <li class="fw-bold"></li>
                <li></li>
                <li class="fw-bold"></li>
                <li></li>
                <li class="mb-3">""</li>
                <img src="@/assets/image/tip/weather/.gif" alt="" style="width:100%">
              </ul>
            </details>
          </li>
        </ul>

        <!-- COVID -->
        <ul class="px-0" v-if="pages === 'covid'">
          <li>
            <p>
              <br />
               0
              <br />
              </p>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <img src="@/assets/image/tip/covid/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <img src="@/assets/image/tip/covid/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4 mb-2"> ()</summary>
              <p class=" mb-2"><br /></p>
              <img src="@/assets/image/tip/covid/.gif" alt="" style="width:100%">
            </details>
          </li>
        </ul>

        <!--  -->
        <ul class="px-0" v-if="pages === 'rapidTest'">
          <li><i class="d-block text-danger mb-2"></i></li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <p>
                <br />
                
              </p>
              <img src="@/assets/image/tip/rapidTest/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4"></summary>
              <p> <br /></p>
              <img src="@/assets/image/tip/rapidTest/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details>
              <summary class="h4 mb-2"></summary>
              <p class=" mb-2"></p>
              <img src="@/assets/image/tip/rapidTest/.gif" alt="" style="width:100%">
            </details>
          </li>
          <li class="ps-2 pt-2 mb-3">
            <details class="mb-2">
              <summary class="h4 mb-2">
                <button type="button" class="bg-primary text-light d-block ms-auto" v-if="localStoragePharmacyData.length > 0" @click="$emit('deleteCollect')"></button>
              </summary>
              <ul>
                <li class="fw-bold"></li>
                <li  class="mb-2"></li>
                <li class="fw-bold"></li>
                <li  class="mb-2"></li>
                <li class="fw-bold"></li>
                <li  class="mb-2"></li>
                <li class="fw-bold"></li>
                <li  class="mb-2">1. </li>
                <li  class="mb-2">2. </li>
              </ul>
              <img src="@/assets/image/tip/rapidTest/.gif" alt="" style="width:100%">
            </details>

          </li>
        </ul>

      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-primary mx-auto w-75" data-bs-dismiss="modal" @click="$emit('tipOpen')"
          v-if="pages === 'weather'"></button>
        <button type="button" class="btn btn-secondary mx-auto" data-bs-dismiss="modal" v-if="pages === 'weather'"></button>
        <button type="button" class="btn btn-secondary mx-auto w-100" data-bs-dismiss="modal" v-if="pages === 'covid' || pages === 'rapidTest'"></button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal.js'
export default {

  props: ['localStoragePharmacyData'],

  inject: ['emitter'],

  emits: ['tipOpen', 'deleteCollect'],

  data () {
    return {
      tipModal: '',
      pages: ''
    }
  },

  mounted () {
    this.tipModal = new Modal(document.getElementById('tipModal'))
    this.emitter.on('openTipModal', (pages) => {
      this.pages = pages
      this.tipModal.show()
    })
    this.emitter.on('closeTipModal', () => {
      this.tipModal.hide()
    })
  },

  unmounted () {
    this.emitter.off('openTipModal')
  }

}
</script>
